import moment from 'moment';
import _ from 'lodash';
import async from 'lodash';
import uuid from 'uuid';
import {dmy} from '../helpers';

export class Transac {

  constructor({name, valueDate=moment().startOf('day').toDate(), server, user, processId, locked=false, compound=false} = {}){
    this.id = uuid();
    this.name = name;
    this.valueDate = valueDate;
    this.processingTime = new Date();
    if(server) this.server = server;
    if(user) this.user = user;
    if(processId) this.processId = processId;
    if(compound){
      this.compound = true;
      this.groupId = this.id;
    }
    this.events = [];
  }
}

